.profile__description{
   grid-area: profile__description;
   font-weight: 400;
   font-size: 18px;
   line-height: 22px;
   margin: 0;
   overflow: hidden;
   white-space: nowrap;
   text-overflow: ellipsis;
}

@media(max-width: 920px){
   .profile__description{
      font-size: 14px;
      line-height: 17px;
      text-align: center;
   }
   }